* {
    box-sizing: border-box;
}

html,
body {
    margin: 0;
    padding: 0;
    font-family: Arial, Helvetica, sans-serif;
    display: flex;
    justify-content: center;
    height: 100%;
    width: 100%;
}

button {
    background-color: white;
    font-size: 2rem;
    margin: .3rem;
    padding: 1rem;
    max-width: 21rem;
    border: .1rem solid #ddd;
    border-radius: .6rem;
    transition: all .3s ease-in-out;
}

button:hover {
    background-color: #007097;
    color: white;
}