#container {
    margin: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
}

#buttons {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

#images {
    margin: 3rem 0 0 0;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-evenly;
    max-width: 50rem;
}

.image {
    margin: 0 0 .6rem 0;
    height: 12rem;
    width: 20rem;
    object-fit: cover;
    border-radius: .6rem;
    transition: all .2s ease-in-out;
    
    &:hover {
        scale: 1.05;
        box-shadow: .05rem .05rem .5rem .03rem rgba(0, 0, 0, .6);
    }
}